import { useContext, useState } from 'react';
import Header from '../components/header';
import FirebaseContext from '../context/firebase';
import UserContext from '../context/user';
import useUser from '../hooks/use-user';
import { storage } from '../lib/firebase';
import { updateUserInformation, updateUserReputationUp } from '../services/firebase';
import AvatarCrop from '../components/AvatarCrop';
import ImageCrop from '../components/ImageCrop';

export default function Settings(){
    const [motto, setMotto] = useState('');
    const [birthyear, setBirthyear] = useState('');
    const [mood, setMood] = useState('');
    const [status, setStatus] = useState('');
    const [zodiac, setZodiac] = useState('');
    const [snapchat, setSnapchat] = useState('');
    const [instagram, setInstagram] = useState('');

    const [image, SetImage] = useState(null);
    const [image2, SetImage2] = useState(null);

    const { firebase } = useContext(FirebaseContext);
    const { user: loggedInUser } = useContext(UserContext);
    const { user } = useUser(loggedInUser?.uid);

    const updateUserSettings = async () => {
      updateUserInformation(user.docId, motto, user.motto, birthyear, user.birthyear, mood, user.mood, status, user.status, zodiac, user.zodiac, snapchat, user.snapchat, instagram, user.instagram); 
    };

    const handleChange = e =>{
        if(e.target.files[0]) { // eğer kullanıcı bir resim seçtiyse
          SetImage(e.target.files[0]);
          console.log(e.target.files[0]);
        }
      };
    
      const handleChangeProfile = e =>{
        if(e.target.files[0]) { // eğer kullanıcı bir resim seçtiyse
          SetImage2(e.target.files[0]);
        }
      };
    
    //generates random id;
    let guid = () => {
      let s4 = () => {
          return Math.floor((1 + Math.random()) * 0x10000)
              .toString(16)
              .substring(1);
      }
      //return id of format 'aaaaaaaa'-'aaaa'-'aaaa'-'aaaa'-'aaaaaaaaaaaa'
      return s4() + s4() + '-' + s4() + '-' + s4() + '-' + s4() + '-' + s4() + s4() + s4();
    }
    
    //"c2181edf-041b-0a61-3651-79d671fa3db7"
      
      /*
      const updateUserSettings = async () => {
        updateUserInformation(user.docId, motto, user.motto, birthyear, user.birthyear, mood, user.mood, status, user.status, zodiac, user.zodiac); 
      };
    */  
    
    const handleUpload = () => {
      if(localStorage.getItem('myData') != null){ 
        const a = guid();
        const uploadTask = storage.ref(`images/${a}`).putString(localStorage.getItem('myData').split(',')[1], "base64", {contentType: 'image/png'});
        uploadTask.on(
          "state_changed",
          snapshot => {
            /*
            const progress = Math.round(
              (snapshot.bytesTransferred / snapshot.totalBytes) * 100
            );
            setProgress(progress);
            */
          },
          error => {
            console.log(error);
          },
          () => {
            storage
              .ref("images")
              .child(a)
              .getDownloadURL()
              .then(url => {
                handleFirebase(url);
            });
          }
        );
      }
    };
    
    const handleUploadProfile = () => {
        if(localStorage.getItem('myData2') != null){
          const a = guid();
          const uploadTask = storage.ref(`avatars/${a}`).putString(localStorage.getItem('myData2').split(',')[1], "base64", {contentType: 'image/png'});
          uploadTask.on(
            "state_changed",
            snapshot => {
              /*
              const progress = Math.round(
                (snapshot.bytesTransferred / snapshot.totalBytes) * 100
              );
              setProgress(progress);
              */
            },
            error => {
              console.log(error);
            },
            () => {
              storage
                .ref("avatars")
                .child(a)
                .getDownloadURL()
                .then(url => {
                  handleFirebaseProfile(url);
                });
              }
          );
        }
        localStorage.clear();
        alert("Değişiklikler kaydedildi.")
    };
    
    const handleFirebase = (url) =>{   
      if(user.reputation < 7){
        updateUserReputationUp(user.docId,user.reputation)
      }
        firebase
        .firestore() // firestore içinde
        .collection('photos')  // photos içinde
        .add({ // ekle
          userId: user.userId,
          permaID: user.userId,
          imageSrc: url
        });
        localStorage.clear();
        alert("Fotoğraf yüklendi.")
      }
    
    const handleFirebaseProfile = (url) =>{
        firebase
        .firestore() // firestore içinde
        .collection('users')  // users içinde
        .doc(user.docId)
        .update({
          userPicture : url
        });
    
        alert("Avatar değiştirildi.")

    }


    return (
        <div className="bg-gray-background">
            <Header />
            <div className="mx-auto max-w-screen-lg px-2">

                <input
                  maxLength="160"
                  aria-label="Biyografinizi giriniz"
                  type="text"
                  placeholder="Biyografi"
                  className="text-sm text-gray-base w-full mr-3 py-5 px-4 h-2 border border-gray-primary rounded mb-2"
                  onChange={({ target }) => setMotto(target.value)}
                />
                <input
                  maxLength="30"
                  aria-label="Instagram hesabınızı giriniz"
                  type="text"
                  placeholder="Instagram"
                  className="text-sm text-gray-base w-full mr-3 py-5 px-4 h-2 border border-gray-primary rounded mb-2"
                  onChange={({ target }) => setInstagram(target.value)}
                />
                <input
                  maxLength="30"
                  aria-label="Snapchat hesabınızı giriniz"
                  type="text"
                  placeholder="Snapchat"
                  className="text-sm text-gray-base w-full mr-3 py-5 px-4 h-2 border border-gray-primary rounded mb-2"
                  onChange={({ target }) => setSnapchat(target.value)}
                />
                
                <input className="my-2" type="radio" id="html" name="birthyear" value="2007" onChange={({ target }) => setBirthyear(target.value)} />
                <label className="mr-1" for="html">2007 </label>
                <input className="mb-4" type="radio" id="css" name="birthyear" value="2006" onChange={({ target }) => setBirthyear(target.value)} />
                <label className="mr-2" for="css">2006 </label>
                <input className="mb-2" type="radio" id="javascript" name="birthyear" value="2005" onChange={({ target }) => setBirthyear(target.value)} />
                <label className="mr-2" for="javascript">2005 </label>
                <input className="mb-2" type="radio" id="javascript" name="birthyear" value="2004" onChange={({ target }) => setBirthyear(target.value)} />
                <label className="mr-2" for="javascript">2004 </label>
                <input className="mb-2" type="radio" id="html" name="birthyear" value="2003" onChange={({ target }) => setBirthyear(target.value)} />
                <label className="mr-2" for="html">2003 </label>
                <input className="mb-2" type="radio" id="css" name="birthyear" value="2002" onChange={({ target }) => setBirthyear(target.value)} />
                <label className="mr-2" for="css">2002 </label>
                <input className="mb-2" type="radio" id="javascript" name="birthyear" value="2001" onChange={({ target }) => setBirthyear(target.value)} />
                <label className="mr-2" for="javascript">2001 </label>
                <input className="mb-2" type="radio" id="javascript" name="birthyear" value="2000" onChange={({ target }) => setBirthyear(target.value)} />
                <label className="mr-2" for="javascript">2000 </label>
                <input className="mb-2" type="radio" id="css" name="birthyear" value="1999" onChange={({ target }) => setBirthyear(target.value)} />
                <label className="mr-2" for="css">1999 </label>
                <input className="mb-2" type="radio" id="javascript" name="birthyear" value="1998" onChange={({ target }) => setBirthyear(target.value)} />
                <label className="mr-2" for="javascript">1998 </label>
                <input className="mb-2" type="radio" id="javascript" name="birthyear" value="1997" onChange={({ target }) => setBirthyear(target.value)} />
                <label className="mr-2" for="javascript">1997 </label><br/>

                <input className="my-2" type="radio" id="html" name="zodiac" value="Koç" onChange={({ target }) => setZodiac(target.value)} />
                <label className="mr-2" for="html">Koç </label>
                <input className="mb-2" type="radio" id="css" name="zodiac" value="Boğa" onChange={({ target }) => setZodiac(target.value)} />
                <label className="mr-2" for="css">Boğa </label>
                <input className="mb-2 form-checkbox" type="radio" id="javascript" name="zodiac" value="İkizler" onChange={({ target }) => setZodiac(target.value)} />
                <label className="mr-2" for="javascript">İkizler </label>
                <input className="mb-2" type="radio" id="javascript" name="zodiac" value="Yengeç" onChange={({ target }) => setZodiac(target.value)} />
                <label className="mr-2" for="javascript">Yengeç </label>
                <input className="mb-2" type="radio" id="html" name="zodiac" value="Aslan" onChange={({ target }) => setZodiac(target.value)} />
                <label className="mr-2" for="html">Aslan </label>
                <input className="mb-2" type="radio" id="css" name="zodiac" value="Başak" onChange={({ target }) => setZodiac(target.value)} />
                <label className="mr-2" for="css">Başak </label>
                <input className="mb-2" type="radio" id="javascript" name="zodiac" value="Terazi" onChange={({ target }) => setZodiac(target.value)} />
                <label className="mr-2" for="javascript">Terazi </label>
                <input className="mb-2" type="radio" id="javascript" name="zodiac" value="Akrep" onChange={({ target }) => setZodiac(target.value)} />
                <label className="mr-2" for="javascript">Akrep </label>
                <input className="mb-2" type="radio" id="css" name="zodiac" value="Yay" onChange={({ target }) => setZodiac(target.value)} />
                <label className="mr-2" for="css">Yay </label>
                <input className="mb-2" type="radio" id="javascript" name="zodiac" value="Oğlak" onChange={({ target }) => setZodiac(target.value)} />
                <label className="mr-2" for="javascript">Oğlak </label>
                <input className="mb-4" type="radio" id="javascript" name="zodiac" value="Kova" onChange={({ target }) => setZodiac(target.value)} />
                <label className="mr-2" for="javascript">Kova </label>
                <input type="radio" id="javascript" name="zodiac" value="Balık" onChange={({ target }) => setZodiac(target.value)} />
                <label className="mr-2" for="javascript">Balık</label><br/>


                <input className="my-2" type="radio" id="html" name="mood" value="Depresyonda" onChange={({ target }) => setMood(target.value)} />
                <label className="mr-2" for="html">Depresyonda </label>
                <input className="mb-2" type="radio" id="css" name="mood" value="Kötü" onChange={({ target }) => setMood(target.value)} />
                <label className="mr-2" for="css">Kötü </label>
                <input className="mb-2" type="radio" id="javascript" name="mood" value="İyi" onChange={({ target }) => setMood(target.value)} />
                <label className="mr-2" for="javascript">İyi </label>
                <input className="mb-4 mt-2" type="radio" id="javascript" name="mood" value="Mükemmel" onChange={({ target }) => setMood(target.value)} />
                <label className="mr-2" for="javascript">Mükemmel</label><br/>

                <input className="my-2" type="radio" id="html" name="status" value="Yok" onChange={({ target }) => setStatus(target.value)} />
                <label className="mr-2" for="html">Yok </label>
                <input type="radio" id="css" name="status" value="Arıyor" onChange={({ target }) => setStatus(target.value)} />
                <label className="mr-2" for="css">Arıyor </label>
                <input className="mb-5" type="radio" id="javascript" name="status" value="Var" onChange={({ target }) => setStatus(target.value)} />
                <label className="mr-2" for="javascript">Var </label>

                <button
                    type="button"
                    className={`bg-gray-base text-white w-full rounded h-8 font-bold mb-8`}
                    onClick={updateUserSettings}
                >
                Ayarla
                </button>

                <AvatarCrop />
                <button
                    type="button"
                    className={`bg-gray-base text-white px-5 rounded h-8 font-bold mb-8 mt-0`}
                    onClick={handleUpload}
                >
                Fotoğraf Yükle
                </button>
                <br/>

                <ImageCrop />
                <button
                    type="button"
                    className={`bg-gray-base text-white px-5 rounded h-8 font-bold mb-8 mt-0`}
                    onClick={handleUploadProfile}
                >
                Avatar Yükle
                </button>
                
            </div>
        </div>
    );
    
}