export async function updateLoggedInUserFollowing(
  loggedInUserDocId, // currently logged in user document id (karl's profile)
  profileId, // the user that karl requests to follow
  isFollowingProfile // true/false (am i currently following this person?)
) {
  return firebase
    .firestore()
    .collection('users')
    .doc(loggedInUserDocId)
    .update({
      following: isFollowingProfile
        ? FieldValue.arrayRemove(profileId)
        : FieldValue.arrayUnion(profileId)
    });
}
export async function updateFollowedUserFollowers(
  profileDocId, // currently logged in user document id (karl's profile)
  loggedInUserDocId, // the user that karl requests to follow
  isFollowingProfile, // true/false (am i currently following this person?)
  booleam
) {
  return firebase
    .firestore()
    .collection('users')
    .doc(profileDocId)
    .update({
      followers: isFollowingProfile
        ? FieldValue.arrayRemove(loggedInUserDocId)
        : FieldValue.arrayUnion(loggedInUserDocId)
    });
  }

  export async function updateLoggedInUserLoving(
    loggedInUserDocId, // currently logged in user document id (karl's profile)
    profileId, // the user that karl requests to follow
    isFollowingProfile, // true/false (am i currently following this person?)
    unfollowCooldown,
    followCount,
    resetMonth,
    resetDay
  ) {
    var a = new Date();
    var m = a.getUTCMonth();
    var d = a.getUTCDate();

    return firebase
      .firestore()
      .collection('users')
      .doc(loggedInUserDocId)
      .update({
        loving: isFollowingProfile
          ? unfollowCooldown ? FieldValue.arrayRemove(profileId) :  FieldValue.arrayRemove('') // Burası cooldown
          : followCount ? FieldValue.arrayUnion(profileId) : FieldValue.arrayRemove(''), // Burası count
        resetDay: unfollowCooldown ? (d+3) : resetDay,
        resetMonth: unfollowCooldown ? (m) : resetMonth
      });
  }
  export async function updateFollowedUserLovers(
    profileDocId, // currently logged in user document id (karl's profile)
    loggedInUserDocId, // the user that karl requests to follow
    isFollowingProfile, // true/false (am i currently following this person?)
    unfollowCooldown,
    followCount
  ) {
    return firebase
      .firestore()
      .collection('users')
      .doc(profileDocId)
      .update({
        lovers: isFollowingProfile
          ? unfollowCooldown ? FieldValue.arrayRemove(loggedInUserDocId) :  FieldValue.arrayRemove('')// Burası
          : followCount ? FieldValue.arrayUnion(loggedInUserDocId) : FieldValue.arrayRemove('')
      });
    }
 
  export async function getPhotos(userId, following) {
    // [5,4,2] => following 
    const result = await firebase
      .firestore()
      .collection('photos')
      .where('userId', 'in', following) // kulanıcının UserID'sinin following kısmında olduğu yerleri al
      .get();
  
    const userFollowedPhotos = result.docs.map((photo) => ({
      ...photo.data(),
      docId: photo.id
    }));
  
    const photosWithUserDetails = await Promise.all(
      userFollowedPhotos.map(async (photo) => {
        let userLikedPhoto = false;
        
        // photo.userId = 2
        const user = await getUserByUserId(photo.userId);
        // raphael
        const { username } = user[0];
        return { username, ...photo, userLikedPhoto };
      })
    );
  
    return photosWithUserDetails;
  }

