  export async function getUserPhotosByUsername(username) {
    const result = await firebase
      .firestore()
      .collection('photos')
      .where('userId', '==', username)
      .get();
  
    return result.docs.map((item) => ({
      ...item.data(),
      docId: item.id
    }));
  }

  export async function isUserFollowingProfile(loggedInUserUsername, profileUserId) {
    const result = await firebase
      .firestore()
      .collection('users')
      .where('username', '==', loggedInUserUsername) // karl (active logged in user)
      .where('following', 'array-contains', profileUserId)
      .get();
  
    const [response = {}] = result.docs.map((item) => ({
      ...item.data(),
      docId: item.id
    }));
  
    return response.userId;
  }

  export async function isUserLovingProfile(loggedInUserUsername, profileUserId) {
    const result = await firebase
      .firestore()
      .collection('users')
      .where('username', '==', loggedInUserUsername) // karl (active logged in user)
      .where('loving', 'array-contains', profileUserId)
      .get();
  
    const [response = {}] = result.docs.map((item) => ({
      ...item.data(),
      docId: item.id
    }));
  
    return response.userId;
  }
  
  export async function toggleFollow(
    isFollowingProfile,
    activeUserDocId,
    profileDocId,
    profileUserId,
    followingUserId
  ) {
    // 1st param: karl's doc id
    // 2nd param: raphael's user id
    // 3rd param: is the user following this profile? e.g. does karl follow raphael? (true/false)
    await updateLoggedInUserFollowing(activeUserDocId, profileUserId, isFollowingProfile);
  
    // 1st param: karl's user id
    // 2nd param: raphael's doc id
    // 3rd param: is the user following this profile? e.g. does karl follow raphael? (true/false)
    await updateFollowedUserFollowers(profileDocId, followingUserId, isFollowingProfile);
  }

  export async function toggleLove(
    isFollowingProfile,
    activeUserDocId,
    profileDocId,
    profileUserId,
    followingUserId,
    unfollowCooldown,
    followCount,
    resetMonth,
    resetDay
  ) {
    // 1st param: karl's doc id
    // 2nd param: raphael's user id
    // 3rd param: is the user following this profile? e.g. does karl follow raphael? (true/false)
    await updateLoggedInUserLoving(activeUserDocId, profileUserId, isFollowingProfile,unfollowCooldown,followCount,resetMonth,resetDay);
  
    // 1st param: karl's user id
    // 2nd param: raphael's doc id
    // 3rd param: is the user following this profile? e.g. does karl follow raphael? (true/false)
    await updateFollowedUserLovers(profileDocId, followingUserId, isFollowingProfile,unfollowCooldown,followCount);
  }
  
  export async function updateUserInformation(
    loggedInUserDocId, // currently logged in user document id (karl's profile)
    newMotto,  // Users new motto
    oldMotto,  // Users old motto
    newBirthyear,
    oldBirthyear,
    newMood,
    oldMood,
    newStatus,
    oldStatus,
    newZodiac,
    oldZodiac,
    newsnapchat,
    oldSnapchat,
    newinstagram,
    oldInstagram
  ) {
    return firebase
      .firestore()
      .collection('users')
      .doc(loggedInUserDocId)
      .update({
        motto: newMotto
        ? newMotto
        : oldMotto,
        birthyear: newBirthyear
        ? newBirthyear
        : oldBirthyear,
        mood: newMood
        ? newMood
        : oldMood,
        status: newStatus
        ? newStatus
        : oldStatus,
        zodiac: newZodiac
        ? newZodiac
        : oldZodiac,
        snapchat: newsnapchat
        ? newsnapchat
        : oldSnapchat,
        instagram: newinstagram
        ? newinstagram
        : oldInstagram
      });
  }

  export async function deleteImage (photoDocId) {
    return firebase
      .firestore()
      .collection('photos')
      .doc(photoDocId)
      .update(
        {userId : ""
      });
  }

  export async function updateUserReputationUp(
    profileDocId,
    rep
  ) {
    return firebase
      .firestore()
      .collection('users')
      .doc(profileDocId)
      .update({
        reputation : rep + 1
      });
  }