import React from 'react';
import AvatarEditor from 'react-avatar-editor';

class MyEditor extends React.Component {

  constructor(props, context){
    super(props, context);

    this.state = {
      image: '',
      userProfilePic:'',
      editor: null,
      scaleValue: 1
    }
  }

  profileImageChange = (fileChangeEvent) => {
    const file = fileChangeEvent.target.files[0];
    const {type} = file;
    if (type.endsWith('jpeg') || type.endsWith('png')) {
      this.setState({userProfilePic : file});
    }
  };

  onClickSave = () => {
    if (this.editor) {
      const canvasScaled = this.editor.getImageScaledToCanvas().toDataURL();
      this.setState({image : canvasScaled});
      localStorage.setItem('myData2',canvasScaled)
    }
  }

  onScaleChange = (fileChangeEvent) => {
    const scaleValue = parseFloat(fileChangeEvent.target.value);
    this.setState({scaleValue})
    console.log(this.state.scaleValue);
  }

  setEditorRef = (editor) => (this.editor = editor)

  render() {
    return (
      <div className="mb-0 pb-0">
        <AvatarEditor
          ref={this.setEditorRef}
          image={this.state.userProfilePic}
          width={250}
          height={250}
          border={25}
          scale={this.state.scaleValue}
        />
        <input style={{width:'50%'}} type="range" value={this.state.scaleValue} min="1" max="10" onChange={this.onScaleChange} /> <br/>
        <input type="file" accept="image/png, image/jpeg" onChange={this.profileImageChange} /><br/>
        <button
          type="button"
          className={`bg-gray-base text-white px-5 rounded h-8 font-bold mb-8`}
          onClick={this.onClickSave}
        >
          Kırp
        </button>
        <img src={this.state.image}/>
      </div>
    )
  }
}

export default MyEditor